wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import App from './App'
import store from './store/store.js'

import Vue from 'vue'
import './uni.promisify.adaptor'
Vue.config.productionTip = false
App.mpType = 'app'

